import * as vscode from 'vscode';
import { v4 as uuid } from 'uuid';
import fetch from 'node-fetch';
import { Agent } from 'https';
import difference from 'lodash.difference';

import { getLogger } from './common/logger';
import * as nls from 'vscode-nls';
import { AuthProviderType } from './util';
import { promiseFromEvent } from './common/utils';
import { commonExtension } from './commonExtension';

const localize = nls.loadMessageBundle();

export const isCloudStudio = commonExtension.isCloudStudio;

const apiOrigin = isCloudStudio ? commonExtension.origin : 'https://wx.cloudstudio.net/api';

let logger = getLogger(AuthProviderType.github);

interface AccessToken {
  team: string;
  accessToken: string;
  providerId: string;
}

interface Result<T> {
  code: number;
  data: T;
  msg: string;
  semanticization: string;
}

export const defaultScopes = ['user', 'user:email', 'read:user', 'write:public_key', 'repo', 'workflow'];

let cookieCache: string;
async function getCookie() {
  if (!cookieCache) {
    const cookie: string | undefined = await vscode.commands.executeCommand('cloudstudio.getCookie');

    if (cookie) {
      const cookieArr = cookie.split(';');
      cookieCache = cookieArr
        .filter((item) => !!item)
        .map((item) => {
          const [key, value] = item.split('=') || [];
          const val = encodeURIComponent(value);
          return `${key}=${val}`;
        })
        .join(';');
    }
  }

  return cookieCache;
}
async function getAccessToken(): Promise<AccessToken[]> {
  let baseUrl = new URL(apiOrigin);
  baseUrl.pathname = '/api/integrations/github/access_token';

  return fetch(baseUrl.toString(), {
    headers: {
      cookie: await getCookie(),
    },
    agent: new Agent({ rejectUnauthorized: false }),
  })
    .then(async (result) => {
      const json = (await result.json()) as Result<AccessToken[]>;

      logger.trace(`get accessTokens result: ${baseUrl.toString()}`, JSON.stringify(json));

      if (json.code === 0) {
        json.data.forEach((item) => {
          item.accessToken = item.accessToken.replace('Bearer ', '');
        });

        return json.data;
      }

      logger.error(`get accessTokens error: ${json.msg || json.semanticization}`);

      return [];
    })
    .catch((e) => {
      logger.error(`get accessTokens fail: ${e.message ?? e}`);

      return [];
    });
}
let time: number | undefined;

function getAccessTokenBySign(baseUrl: URL, sign: string, onDidAuthorize: vscode.EventEmitter<string | Error>) {
  baseUrl.pathname = `/api/public/oauth/code/access_token/${sign}`;

  time = setTimeout(async () => {
    fetch(baseUrl.toString(), {
      headers: {
        cookie: await getCookie(),
      },
      agent: new Agent({ rejectUnauthorized: false }),
    })
      .then(async (result) => {
        const json = (await result.json()) as Result<AccessToken>;

        if (json.code === 0 && json.data) {
          onDidAuthorize.fire(json.data.accessToken.replace('Bearer ', ''));
          return;
        }

        if (json.code !== 0) {
          logger.error(`get accessToken error:${json.semanticization || json.msg}`);
        }

        getAccessTokenBySign(baseUrl, sign, onDidAuthorize);
      })
      .catch((e) => {
        logger.error(`get AccessToken fail: ${e.message ?? e}`);

        getAccessTokenBySign(baseUrl, sign, onDidAuthorize);
      });
  }, 1500) as any;
}

export async function getCloudStudioSessions(sessions: vscode.AuthenticationSession[]) {
  if (isCloudStudio && !(await commonExtension.getUserInfo()).authenticationUserInfo.trial) {
    let accessTokns = await getAccessToken();

    accessTokns = accessTokns.filter(
      (item) => sessions.findIndex((local) => item.accessToken === local.accessToken) < 0,
    );

    if (accessTokns.length > 0) {
      const serverSession = await Promise.all<vscode.AuthenticationSession[]>(
        accessTokns.map((item) => {
          return {
            id: uuid(),
            accessToken: item.accessToken,
            account: { label: item.team, id: item.providerId },
            scopes: ['repo', 'workflow', 'user:email', 'read:user'],
          };
        }),
      );

      sessions.splice(0, 0, ...serverSession);
    }
  }
}

function getCookieValue(name: string, cookie: string) {
  const match = cookie.match('(^|[^;]+)\\s*\\b' + name + '\\b\\s*=\\s*([^;]+)');
  return match ? match.pop() : '';
}

function generateCsrfCode(cookie: string) {
  // 优先取腾讯云 skey，若没有则取 SaaS 的 cloudstudio-session
  const skey = getCookieValue('skey', cookie) || getCookieValue('cloudstudio-session', cookie);
  if (!skey) {
    return null;
  }
  let hash = 5381;
  for (let i = 0, len = skey.length; i < len; ++i) {
    hash += (hash << 5) + skey.charCodeAt(i);
  }
  const csrfCode = hash & 0x7fffffff;
  return csrfCode;
}

export async function cloudStudioLogin(scopes: string[]) {
  const sign = uuid();

  scopes.push('user', 'write:public_key');

  let baseUrl = new URL(apiOrigin);

  baseUrl.pathname = `/api/public/github/authorize`;
  const url = new URL(baseUrl.toString());
  url.searchParams.append('scope', scopes.join(','));

  const cookie: string = (await vscode.commands.executeCommand('cloudstudio.getCookie')) || '';
  const xsrfToken = generateCsrfCode(cookie);
  if (xsrfToken) {
    url.searchParams.append('_csrf', xsrfToken.toString());
  }
  const xsrfTokenPatamStr = xsrfToken ? `?_csrf=${xsrfToken}` : '';

  let redirectUri = apiOrigin;

  logger.trace('difference', difference(scopes, defaultScopes));

  if (
    isCloudStudio &&
    !(await commonExtension.getUserInfo()).authenticationUserInfo.trial &&
    difference(scopes, defaultScopes).length === 0
  ) {
    redirectUri += `/integrations/associate/${sign}${xsrfTokenPatamStr}`;
  } else {
    redirectUri += `/public/oauth/code/callback/${sign}${xsrfTokenPatamStr}`;
  }

  url.searchParams.append('redirect_uri', redirectUri);

  // 这里使用 string 作为 openExternal 的入参，是因为转换为 Uri 之后会导致 redirect_uri urlEncode 异常
  vscode.env.openExternal(url.toString() as any);

  const onDidAuthorize = new vscode.EventEmitter<string | Error>();

  getAccessTokenBySign(baseUrl, sign, onDidAuthorize);

  return await doLoginCoding(onDidAuthorize);
}

async function doLoginCoding(onDidAuthorize: vscode.EventEmitter<string | Error>) {
  return await vscode.window.withProgress<string>(
    {
      location: vscode.ProgressLocation.Notification,
      title: localize('signingIn', 'Signing in to github.com...'),
      cancellable: true,
    },
    async (_, token) => {
      const codeExchangePromise = promiseFromEvent<string | Error, string>(
        onDidAuthorize.event,
        (result: string | Error, resolve, reject) => {
          if (result instanceof Error) {
            reject(result);
          } else {
            resolve(result);
          }
        },
      );

      let accessToken = '';
      try {
        accessToken = await Promise.race([
          codeExchangePromise.promise,
          new Promise<string>((_, reject) => setTimeout(() => reject('Cancelled'), 60000)),
          promiseFromEvent<any, any>(token.onCancellationRequested, (_, __, reject) => {
            reject('User Cancelled');
          }).promise,
        ]);
      } finally {
        codeExchangePromise?.cancel.fire();
        clearTimeout(time);
      }

      return accessToken;
    },
  );
}
